
import { menuList } from "@/utils/menuList"
import { getCurrentInstance } from '@vue/runtime-core'
export default {
  name: "DbHeader",
  components: {},
  setup() {
    // @ts-ignore
    const { ctx } = getCurrentInstance()
    const menulist = menuList
    // 页面的跳转
    const clickMenu = function (path: string) {
      ctx.$router.push('/' + path)
    }
    return {
      menulist, clickMenu
    }
  }
}
